<template>
  <div class="login">
    <mt-header title=""
               style="-webkit-transform: translateZ(0);background: -webkit-gradient(linear,left top,right top,from(#0af),to(#0085ff))"
               slot="header">
      <!--<router-link slot="left" to="/home">-->
      <!--<mt-button icon="back"></mt-button>-->
      <!--</router-link>-->
      <!--<router-link to="/register" slot="right">-->
      <!--<mt-button></mt-button>-->
      <!--</router-link>-->
    </mt-header>
    <form :model="loginForm" class="loginForm">

      <!--<mt-field label="" placeholder="" v-model="loginForm.username"></mt-field>-->
      <!--<mt-field label="" placeholder="" v-model="loginForm.password"></mt-field>-->
      <section>
        <div>
          <img src="../../assets/img/5.png">
        </div>
      </section>
      <section>
        <div>
          <input type="text" placeholder="/" v-model="loginForm.username">
        </div>
      </section>
      <section>
        <div>
          <input type="password" placeholder="" v-model="loginForm.password">
        </div>
      </section>
      <section>
        <mt-button style="background: #26a2ff" @click="loginSubmit"></mt-button>
      </section>
      <!--<section class="test">-->
      <!--<p></p>-->
      <!--<p>/:2014874236/123456</p>-->
      <!--<p>/:2014874239/123456</p>-->
      <!--<p>/:2014874207/654321</p>-->
      <!--</section>-->
    </form>
    <!--  -->
    <!-- <div class="img_change_img">
            <img v-show="captchaCodeImg" :src="captchaCodeImg">
            <div class="change_img" @click="getCaptchas">
                <p></p>
                <p></p>
            </div>
        </div> -->
  </div>
</template>
<script>
  import { login } from '../../global/api'
  import { MessageBox } from 'mint-ui'

  export default {
    data () {
      return {
        loginForm: {
          username:'',
          password:''
        },
        form: '',
        // loginStatus:store.state.loginStatus
        // captchaCodeImg:''
      }
    },
    mounted: function () {
    },
    methods: {
      loginSubmit: function () {
        login(this.loginForm.username, this.loginForm.password).then(res => {
          if (res.data.code === 0) {
            MessageBox.alert(res.data.message, '')
            this.loginForm.username = ''
            this.loginForm.password = ''
          } else {
            let redirect = this.$route.query.redirect
            this.$router.push({path: redirect ? redirect : '/'})
            // this.loginStatus=true;
            // store.dispatch('setloginStatus', true)
            // console.log("loginloginStatus",this.loginStatus);
          }
        })



      }
      // getCaptchas:function(){
      // 	this.$http.get(api.captchas).then((response) => {
      // 		console.log("",response);
      // 	},() => {
      // 		console.log("");
      // 	})
      // }
    }
  }
</script>
<style>
  /**/
  .login .mint-header {
    background-color: #1b1b20;
    color: #fff;
    font-size: 0.8rem;
    z-index: 100;
    height: 2rem;
    line-height: 2.5rem;
  }

  /**/
  .login button, .login button label {
    cursor: pointer;
    height: 1.7rem;
  }

  .login .mintui {
    font-size: 0.8rem;
  }

  /**/
  .login .loginForm section {
    line-height: 2rem;
    width: 100%;
    text-align: center;
    margin-top: 1rem;
    margin-bottom: 1.2rem;
  }

  .login .loginForm section:nth-child(1) {
    margin-top: 2rem;
  }

  .login .loginForm section div {
    width: 80%;
    border: 1px solid #e0e0e0;
    display: inline-block;
    background: #fff;
  }

  .login .loginForm section div input {
    outline: none;
    border: 0;
    font-size: 0.7rem;
    width: 90%;
  }

  /**/
  .login .loginForm button {
    width: 80%;
    background: #ff3600;
    border-radius: 0px;
    color: #fff;
    text-align: center;
    margin-top: 1.2rem;
    height: 1.7rem;
    font-size: 0.7rem;
  }

  .login .test {
    font-size: 0.7rem;
  }
</style>



